raul-action {
  display: inline-block;
}

raul-action .r-action__button {
  font-size: 1.25rem;
  border-radius: 0.125rem;
  width: 40px;
  height: 40px;
  line-height: 20px;
  border: solid 1px transparent;
}

raul-action .r-action__button--active {
  color: #0076CC;
}

body[modality=keyboard] raul-action .r-action__button:focus {
  outline: none;
  border: 1px solid #0076CC;
}